*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #d2dbdd;
}


.heading{
  padding: 30px 0;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Raleway', sans-serif;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  background-color: #fff;
  font-size: 1.5rem;
  margin-bottom: 30px;
}
.slotmachine{
  width: 400px;
  height: 400px;
  margin-top: 100px;
  margin-left: 550px;
  background-color: #f2f2f2;
  border: 2px solid #333;
  border-radius: 5px;
}
.h1{
  font-size: 5px;
}
